.Modal {
  position: fixed;
  z-index: 500;
  background-color: rgb(255, 255, 255);
  width: 70%;
  border: 1px solid rgb(204, 204, 204);
  -webkit-box-shadow: 1px 1px 1px black;
          box-shadow: 1px 1px 1px black;
  padding: 16px;
  left: 15%;
  top: 10%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.Modal .ModalBody {
  width: 100%;
  height: 70vh; /* 70% height of the viewport that is defined in index.html */
  overflow: auto; /* Added overflow: auto to make Modal scrollable */
}

/* for large screen */
/* More than or at 600px of width */
@media (min-width: 600px) {
  .Modal {
    width: 500px;
    left: calc(50% - 250px);
  }
}

/* for large screen */
/* More than or at 600px of width and height */ 
@media screen and (min-height: 600px) and (min-width: 600px) {
  .Modal .ModalBody {
    height: auto;
  }
}



