.NavigationItem {
  margin: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
}

.NavigationItem a {
  color: rgb(143, 92, 44);
  text-decoration: none;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
  color: rgb(64, 164, 200);
}

/* Media query for desktop device */
@media (min-width: 500px) {
  .NavigationItem {
    margin: 0;
    /* box-sizing: border-box; */
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    width: auto;
    -ms-flex-align: center;
        align-items: center;
  }
  
  .NavigationItem a {
    color: white;
    /* text-decoration: none; */
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4 px solid transparent;
    /* box-sizing: border-box;
    display: block; */
  }
  
  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active {
    background-color: rgb(143, 92, 44);
    border-bottom: 4px solid rgb(64, 164, 200);
    color: white;
  }
}


