.BuildControls {
  width: 100%;
  /* height: 320px; */
  background-color: rgb(207, 143, 46);
  display: -ms-flexbox;
  display: flex;
  /* flex-flow: column;
  align-items: center; 
  justify-content: space-around; */
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  -ms-flex-align: start;
      align-items: flex-start; 
  -webkit-box-shadow: 0 2px 1px rgb(95, 90, 90); 
          box-shadow: 0 2px 1px rgb(95, 90, 90);
  margin: auto; 
  padding-top: 10px;
  padding-bottom: 10px;
}

.Price {
  width: 100%;
  background-color: rgb(207, 143, 46);
}

.Price span {
  padding-right: 16px;
  font-weight: bold;
}

.Price .OrderButton {
  margin-top: 5px;
}

.OrderButton {
  background-color: #DAD735;
  outline: none;
  cursor: pointer;
  border: 1px solid #966909;
  color: #966909;
  font-family: inherit;
  font-size: 16px;
  padding: 6px 6px; 
  -webkit-box-shadow: 2px 2px 2px #966909; 
          box-shadow: 2px 2px 2px #966909;
}

.OrderButton:hover, .OrderButton:active {
  background-color: #A0DB41;
  border: 1px solid #966909;
  color: #966909;
}

.OrderButton:disabled {
  background-color: #C7C6C6;
  cursor: not-allowed;
  border: 1px solid #ccc;
  color: #888888;
}

.OrderButton:not(:disabled) {
  -webkit-animation: enable 0.3s linear;
          animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  100% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}

@keyframes enable {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  100% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}



